<template>
	<view class='wrapper'>
		<scroll-view
			:scroll-y="true"
			:scroll-into-view="currentId"
			:scroll-with-animation="true"
			:enable-back-to-top="true"
			class="scroll_view"
		>
			<view class="form_wrapper first">
				<view class="qx-from-group">
					<view class="title">姓名<text class="text-red">*</text></view>
					<input placeholder="请输入姓名" name="input" v-model="paramInfo.memberName" :disabled="true"></input>
				</view>
				<view class="qx-from-group">
					<view class="title">手机<text class="text-red">*</text></view>
					<input 
						placeholder="请输入手机号" 
						name="input" 
						v-model="paramInfo.memberMobile" 
						:disabled="fromType==1"
						@click="fromType==1 && tel(paramInfo.memberMobile)"
						maxlength="11"
					></input>
				</view>
				<view class="qx-from-group no-border">
					<view class="title long">是否为大客户<text class="text-red">*</text></view>
					<view class="radio_group">
						<view class="radio_item" @click="changeVol(1)">
							<image src="../../static/select.png" v-if="paramInfo.isVol==1"></image>
							<image src="../../static/unselect.png" v-else></image>
							<text>是</text>
						</view>
						<view class="radio_item" @click="changeVol(0)">
							<image src="../../static/select.png" v-if="paramInfo.isVol==0"></image>
							<image src="../../static/unselect.png" v-else></image>
							<text>否</text>
						</view>
					</view>
				</view>
				
			</view>
			<view class="add" @click="add">
				<text>新增意向</text>
				<image src="../../static/add.png"></image>
			</view>
			<view class="yixiang" >
				<view 
					:class="['form_wrapper',item.animationType==2?'animation-slide-right animation-reverse':'animation-slide-left']" 
					v-for="(item,index) in orderList" 
					:key="index"
				>
						<view class="delete" v-if="!item.orderId" @click="deleteItem(item,index)">
							<image src='../../static/delete.png' class="delete_img"></image>
						</view>
						<view :class="['qx-from-group',item.orderId?'no_edit':'']">
							<view class="title">意向车型({{index+1}})<text class='text-red'>*</text></view>
							<picker
								@change="carChange($event,item)" 
								:value="item.carIdx"
								:range="carList" 
								class="picker"
								:disabled="item.orderId"
								range-key="showName"
							>
								<view class="picker">
									<view v-if="item.orderId">{{item.carTypeName}}</view>
									<view v-else :class="[!item.carIdx?'placeholder_style':'']">
										{{item.carIdx!=null? carList[item.carIdx].name : '请选择意向车型'}}
									</view>
								</view>
							</picker>
							<view class="img_w flex-row-start" v-if="!item.orderId">
								<image v-if="item.carTypeName" src="../../static/clear_black.png" class="img" @click="clearValue(item,1)"></image>
								<image v-else src="../../static/down_arrow.png" class="img" ></image>
							</view>
						</view>
						<view :class="['qx-from-group',item.orderId?'no_edit':'']">
							<view class="title">意向4S店<text class='text-red'>*</text></view>
							<input 
								placeholder="请选择意向4S店"
								name="input"
								:disabled="true"
								 @click='goSelectShop(item,index)' 
								 v-model="item.store4sName"
							 ></input>
							 <!-- 已创建订单 隐藏 图片 -->
							 <view class="img_w flex-row-center" v-if="!item.orderId">
									<image v-if="item.store4sName" src="../../static/clear_black.png" class="img" @click="clearValue(item,2)"></image>
									<image  v-else src="../../static/down_arrow.png" class="img"></image>
							 </view>
						</view>
						<view :class="['qx-from-group',item.orderId?'no_edit':'','no-border']">
							<view class="title">询价情况</view>
							<input 
								placeholder="请输入已询底价" 
								name="input" 
								v-model="item.inquiryInfo"
								:disabled="item.orderId"
								type="digit"
							></input>
							<view class="unit" :id="'dom'+index">万</view>
							<view class="img_w flex-row-center" v-if="!item.orderId">
								<image v-if="item.inquiryInfo" src="../../static/clear_black.png" class="img" @click="clearValue(item,3)"></image>
							</view>
						</view>
				</view>
			</view>
		</scroll-view>
		<view class='btn_wrapper'>
			<view class='cancel' @click="back">
				<button class="cu-btn block lg">
						取消
				</button>
				</view>
			<view class="sure">
				<button class="cu-btn block lg" @click="createOrder" :disabled="btnDisabled">
					保存
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currentId:'dom0',
				fromType:0,//0 新增 , 1 编辑
				carList:[],//车辆列表
				clientInfo:{},
				paramInfo:{
					memberId:'',//客户Id	Integer	是	
					memberName:'',//	客户姓名	String	是	
					memberMobile:'',//	客户手机号	String	是	
					isVol:0,	//是否大客户	Integer	是	0-否，1-是
					orderList:''//客户订单列表
				},
				btnDisabled:false,
				orderList:[]//客户订单
			};
		},
		methods:{
			carChange(e,item){
				var _idx=e.detail.value //选的第几个
				item.carIdx=_idx
				item.carTypeId=this.carList[_idx].id
				item.carTypeName=this.carList[_idx].name
			},
			tel(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
			add(){
				this.orderList.push({
					carTypeId:null, //车辆 类型id
					carTypeName:null, // 类型名称
					store4sId:null, // 4s店id
					store4sName:null, // 4s店名称
					inquiryInfo:'',//询价情况
					carIdx:null ,//记录所选下标,
					animationType:1 //1 是添加
				})
				this.currentId="dom"+(this.orderList.length-1)
				
			},
			deleteItem(item,index){
				uni.showModal({
					title:"提示？",
					content:`是否删除第${index+1}条购车意向？`,
					success:(res)=>{
						if(res.confirm){
							item.animationType=2 //删除
							setTimeout(()=>{
								this.orderList.splice(index,1)
							},500)
						}
					}
				})
			},
			clearValue(item,type){
				if(type==1){ //清意向车型
					item.carTypeId=null
					item.carTypeName=null
					item.carIdx=null
				}else if(type==2){ //清意向4s店
					item.store4sId=null; // 4s店id
					item.store4sName=null// 4s店名
				}else{
					item.inquiryInfo=null
				}
			},
			goSelectShop(item,index){
				if(item.orderId){ //已存在订单不让修改
					return
				}
				this.perfectNavigateTo({
					url:"/pages/fourShop/fourShop?fromPage=1&idx="+index
				})
			},
			changeVol(vol){
				if(this.fromType==0){ //只有新绑定可以修改
					this.paramInfo.isVol=vol
				}
			},
			getCarList(){
				this.request("/cartype/spinner").then((res)=>{
					if(res.resultCode==0){
						this.carList=res.data
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			back(){
				uni.showModal({
					title: '提示',
					cancelText:"再看看",//默认是“取消”
					confirmText:"放弃",
					content: '是否放弃本次编辑？',
					success: (res)=>{
						if (res.confirm) {
							uni.navigateBack({
								delta:1
							})
						}
					}
				})
			},
			pageInit(){
				this.getCarList()
			},
			
			createOrder(){
				if(!this.paramInfo.memberMobile){ uni.showToast({title: '请输入手机号',icon:'none'});return}
				for(var i=0;i<this.orderList.length;i++){
					if(!this.orderList[i].store4sName && !this.orderList[i].carTypeName){
						continue
					}else{ //做判断
						if(!this.orderList[i].store4sName){uni.showToast({title: `请选择第${i+1}条购买意向的4S店`,icon:'none'});return}
						if(!this.orderList[i].carTypeName){uni.showToast({title: `请选择第${i+1}条购买意向的车型`,icon:'none'});return}
						if(this.orderList[i].inquiryInfo==null || this.orderList[i].inquiryInfo==undefined){
							delete this.orderList[i].inquiryInfo
						}
						// if(!this.orderList[i].inquiryInfo){uni.showToast({title: `请选择第${i+1}条购买意向的询价情况`,icon:'none'});return}
					}
				}
				
				/* 去除已存在订单 */
				var _addOrder=[]
				this.orderList.forEach((item)=>{
					// 如果没有订单号 并且 都填了
					if(!item.orderId && item.carTypeName && item.store4sName){
						_addOrder.push(item)
					}
				})
				this.paramInfo.orderList=JSON.stringify(_addOrder)
				
				var requestUrl; //请求接口
				if(this.fromType==0){ //新增
					requestUrl="/order/staff/member/bindAddIntention"
				}else{ //编辑
					requestUrl="/order/staff/member/addIntention"
				}	
				this.btnDisabled=true
				this.request(requestUrl,this.paramInfo).then((res)=>{
					if(res.resultCode==0){
						uni.showModal({
							title: '提示',
							content: res.data,
							showCancel:false,
							success: (res)=>{
								if (res.confirm) {
									uni.navigateBack({
										delta:1
									})
								}
							}
						})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
						this.btnDisabled=false
					}
				}).catch((err)=>{
					this.btnDisabled=false
				})
			},
			getCustomOrderList(){
				this.request("/order/member/orders",{memberId:this.paramInfo.memberId}).then((res)=>{
					if(res.resultCode==0){
						this.orderList=res.data.orderList
						this.paramInfo.memberMobile=res.data.memberMobile
						this.paramInfo.isVol=res.data.isVol
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			}
		},
		onLoad(params){
			this.clientInfo=JSON.parse(params.info)
			this.fromType=params.fromType
			this.paramInfo.memberId=this.clientInfo.userId
			this.paramInfo.memberName=this.clientInfo.name
			this.pageInit()
			if(this.fromType==1){ //编辑
				this.getCustomOrderList()
			}
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
	}
</script>

